*{
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html{
    font-size: 16px;
    scroll-behavior: smooth;
}

body{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-image: linear-gradient(45deg, #2f2f2f 25%, #323232 25%, #323232 50%, #2f2f2f 50%, #2f2f2f 75%, #323232 75%, #323232 100%);
    background-size: 70.71px 70.71px;
    display: flex;
    justify-content: center;
}

input{
    outline: none;
    border: none;
}

button{
    cursor: pointer;
    outline: none;
    border: none;
    background: none;
}

.app{
    position: relative;
    padding: 0 3rem;
    padding-bottom: 3rem;
    max-width: 1366px;
    width:100%;
    min-height: 100vh;
    /* position: relative; */
}

main{
    max-width: 1440px;
    padding: 0 4rem;
    background-image: linear-gradient(136deg, #f5f5f5 20.93%, #ffffff 20.93%, #ffffff 50%, #f5f5f5 50%, #f5f5f5 70.93%, #ffffff 70.93%, #ffffff 100%);
    background-size: 61.90px 59.78px;
   
}

.main{
    padding: 4rem 0;
    padding-bottom: 1rem;
    background: white;
    min-height: 20rem;
}

a{
    text-decoration: none;
    color: black;
}

@media screen and (max-width: 767px){
    .app{
        padding: 0 1rem;
    }
    main{
        padding: 0 2rem;
    }
}

@media screen and (max-width: 350px){
    .app{
        padding: 0 .5rem;
    }
    main{
        padding: 0 1rem;
    }
}
@-webkit-keyframes moveUpDown{0%{-webkit-transform:translateY(0);transform:translateY(0)}50%{-webkit-transform:translateY(-2rem);transform:translateY(-2rem)}100%{-webkit-transform:translateY(0);transform:translateY(0)}}@keyframes moveUpDown{0%{-webkit-transform:translateY(0);transform:translateY(0)}50%{-webkit-transform:translateY(-2rem);transform:translateY(-2rem)}100%{-webkit-transform:translateY(0);transform:translateY(0)}}.card{background:white;overflow:hidden;border-radius:1rem}.card:hover{-webkit-animation:moveUpDown .5s linear 1;animation:moveUpDown .5s linear 1}.card__id{padding-left:1rem;font-family:'Passion One', cursive;color:#bebebe}.card__img{width:100%;padding:2rem 0;background:#F2F2F2}.card__img img{display:block;margin:0 auto}.card__title{font-family:'Titillium Web', sans-serif;padding-left:1rem;font-size:1.2rem;text-transform:capitalize}.card__types{padding-top:.5rem;padding-left:1rem}

.badge{padding:.1rem 1rem;margin-right:.8rem;border-radius:.3rem;font-size:.8rem;color:white;display:inline-flex;justify-content:center;align-items:center;font-weight:600;font-family:'Exo 2', sans-serif}.badge--normal{background:#949494}.badge--fighting{background:#ff7300}.badge--flying{background:#9ba5ff}.badge--poison{background:#ae00ff}.badge--ground{background:#9c4f4f}.badge--rock{background:#475b75}.badge--bug{background:#5eff00}.badge--ghost{background:#292929}.badge--steel{background:#585151}.badge--fire{background:red}.badge--water{background:#1900ff}.badge--grass{background:green}.badge--electric{background:#fbff00}.badge--psychic{background:#ff009d}.badge--ice{background:#00c3ff}.badge--dragon{background:#c04600}.badge--dark{background:#080808}.badge--fairy{background:#ff4d91}.badge--unknown{background:#fff}.badge--shadow{background:#383232}

