@-webkit-keyframes loading{0%{-webkit-transform:rotate(0);transform:rotate(0)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes loading{0%{-webkit-transform:rotate(0);transform:rotate(0)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.loading{display:flex;justify-content:center;align-items:center}.loading--small{padding-top:1rem}.loading--small .pokeball{width:3rem;height:3rem}.loading--small .pokeball__middle{width:1rem;height:1rem;border:2px solid black}.loading--big{background-image:linear-gradient(45deg, #2f2f2f 25%, #323232 25%, #323232 50%, #2f2f2f 50%, #2f2f2f 75%, #323232 75%, #323232 100%);background-size:70.71px 70.71px;width:100vw;height:100vh;top:0;left:0;position:fixed}.loading--big .pokeball{width:6rem;height:6rem}.loading--big .pokeball__middle{width:2rem;height:2rem;border:4px solid black}.loading .pokeball{border-radius:100%;overflow:hidden;position:relative;-webkit-animation:loading 1.5s linear infinite;animation:loading 1.5s linear infinite}.loading .pokeball__top{background-color:red;height:50%;z-index:1}.loading .pokeball__bottom{background-color:white;height:50%;z-index:1}.loading .pokeball__line{position:absolute;width:100%;height:10%;background-color:black;z-index:2;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.loading .pokeball__middle{position:absolute;z-index:3;background-color:white;border-radius:100%;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}

