.search{position:relative;display:flex;justify-content:center;width:100%;padding:1rem 0;background-color:#616161;position:relative;z-index:1}.search::after,.search::before{content:"";position:absolute;width:10rem;height:5rem;z-index:0;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);background-color:#616161}.search::after{border-bottom-left-radius:8rem;border-bottom-right-radius:8rem;top:60%}.search::before{border-top-left-radius:8rem;border-top-right-radius:8rem;top:-60%}.search form{z-index:1;position:relative;width:80%}.search__input{width:100%;height:3.5rem;padding-left:2rem;border:1px solid black;font-size:1.2rem;border-radius:2rem;padding-right:4rem}.search__button{position:absolute;top:0;right:0;width:3.5rem;height:100%;border-radius:50%;font-size:1.2rem;display:flex;justify-content:center;align-items:center}@media screen and (max-width: 767px){.search::after,.search::before{width:8rem;height:4rem}.search::before{border-top-left-radius:8rem;border-top-right-radius:8rem;bottom:60%;top:auto}.search__input{height:3rem;padding-left:1.5rem;border:1px solid black;font-size:1rem;border-radius:2rem;padding-right:3rem}}@media screen and (max-width: 576px){.search__input{height:2.5rem;padding-left:1rem;font-size:.8rem;padding-right:3rem}.search::after,.search::before{width:7rem;height:3.5rem}.search__button{width:3rem;font-size:1rem}}

.err__content{position:relative;display:flex;justify-content:center;align-items:center;flex-direction:row}.err__img{min-width:10rem;max-width:10rem;height:10rem}.err__img img{display:block;width:100%;height:auto}.err__text{font-size:1.3rem;font-family:'Chakra Petch', sans-serif}

@-webkit-keyframes fadeInBtn{0%{opacity:0}100%{opacity:1}}@keyframes fadeInBtn{0%{opacity:0}100%{opacity:1}}@-webkit-keyframes moveUpDownBtn{0%{-webkit-transform:translateY(0);transform:translateY(0)}50%{-webkit-transform:translateY(-1rem);transform:translateY(-1rem)}100%{-webkit-transform:translateY(0);transform:translateY(0)}}@keyframes moveUpDownBtn{0%{-webkit-transform:translateY(0);transform:translateY(0)}50%{-webkit-transform:translateY(-1rem);transform:translateY(-1rem)}100%{-webkit-transform:translateY(0);transform:translateY(0)}}.scroll-top{position:fixed;bottom:2rem;right:2rem;background-image:linear-gradient(136deg, #f5f5f5 20.93%, #fff 20.93%, #fff 50%, #f5f5f5 50%, #f5f5f5 70.93%, #fff 70.93%, #fff 100%);background-size:61.90px 59.78px;width:3rem;height:3rem;display:block;z-index:100;border-radius:50%;font-size:2rem;-webkit-animation:fadeInBtn .2s ease-in;animation:fadeInBtn .2s ease-in}.scroll-top:hover{-webkit-animation:moveUpDownBtn 1s linear infinite;animation:moveUpDownBtn 1s linear infinite}

