.error{background-color:#fff;padding:2rem;border-radius:1rem;max-width:40rem;margin:auto;font-family:'Chakra Petch', sans-serif}.error__404{display:flex;flex-direction:row;align-items:center;justify-content:center;height:150px}.error__404 span{font-size:10rem;line-height:10rem;color:#CCCCCC;display:block}.error__404 img{height:100%;width:auto}.error__content{margin:2rem 0;text-align:center}.error__content h2{font-size:2.5rem;font-weight:900}.error__content p{font-weight:400;font-size:1.1rem;color:#7e7e7e}.error__btn{text-align:center}.error__btn .btn{padding:15px;background-color:#DF3D42;color:#fff;font-size:1.1rem;border-radius:10px;font-family:'Chakra Petch', sans-serif}.error__btn .btn i{margin-right:1rem}@media screen and (max-width: 576px){.error__404{height:100px}.error__404 span{font-size:6.5rem;line-height:6.5rem}}@media screen and (max-width: 320px){.error__404{height:80px}.error__404 span{font-size:5rem;line-height:5rem}.error__content{margin:1rem 0;text-align:center}.error__content h2{font-size:2rem;font-weight:900}.error__content p{font-weight:400;font-size:1rem;color:#7e7e7e}.error__btn .btn{padding:10px;font-size:1rem;border-radius:10px}.error__btn .btn i{margin-right:1rem}}

